













































































import {QuestionTitle,Editor} from '../../components/el-i'
import {Checkbox,CheckboxGroup,Button,Dialog,RadioGroup,Radio,Input,Tag} from 'element-ui'
import IDialog from "@/components/Dialog.vue";
import ElRadioGroup from "element-ui/packages/radio/src/radio-group.vue";
import ElRadio from "element-ui/packages/radio/src/radio.vue";

export default {
    data() {
        return {
            editorOnIndex: '0',
            sureCommit: false,
            title: {
                id: 'topic',
                value: '',
            },
            choices: {
                checked: [],
                isAppend: false,
                data: [
                    {status: true, text: "<p>text1</p>"},
                    {status: true, text: "<p>text2</p>"},
                    {status: true, text: "<p>text3</p>"},
                    {status: true, text: "<p>text4</p>"},
                ]
            },
            analysis:{
                id: 'analysis',
                value: '',
            },
            keyword:[],
            inputVisible: false,
            inputValue: '',
            inputError:false,
            data: null
        }
    },
    components: {
        ElRadio,
        ElRadioGroup,
        IDialog,
        Editor,
        [QuestionTitle.name]: QuestionTitle,
        [Input.name]: Input,
        [Tag.name]: Tag,
        [CheckboxGroup.name]: CheckboxGroup,
        [Checkbox.name]: Checkbox,
        [RadioGroup.name]: RadioGroup,
        [Radio.name]: Radio,
        [Dialog.name]: Dialog,
        [Button.name]: Button,
        [Editor.name]: Editor,
    },
    methods: {
        handleClose(tag) {
            this.keyword.splice(this.keyword.indexOf(tag), 1);
        },
        showInput() {
            this.inputVisible = true;
            this.$nextTick(function(){
                this.$refs.saveTagInput.$refs.input.focus();
            });
        },
        handleInputConfirm() {
            let inputValue = this.inputValue;
            let isNew = this.keyword.indexOf(inputValue) == -1
            if (isNew && inputValue) {
                this.keyword.push(inputValue);
                this.inputVisible = false;
                this.inputValue = '';
            }else{
                this.inputError = true
                this.$refs.saveTagInput.$refs.input.focus();
            }
        },
        getEditorChoiceValue: function (value, index) {
            this.choices.data[index].text = value
        },
        getEditorValue: function(value){
            this.title.value = value;
        },
        getAnalysisValue: function(value){
            this.analysis.value = value;
        },
        toChar: function (n: number) {
            return String.fromCharCode(65 + n);
        },
        addChoice: function () {
            this.choices.data.push({status: true, text: "<p>text</p>"});
            this.editorOnIndex = 'choice' + (this.choices.data.length-1);
        },
        removeChoice: function (index) {
            this.choices.data[index].status = false;
            this.choices.data = this.choices.data.filter(function (value) {
                return value.status;
            })
        },
        show: function () {
            console.log('show')
        },
        commit: function () {
            let data = {

            }
            this.$axios({
                url: this.$urls.chooseSave,
                method: "post",
                data: data,
            }).then(function (data) {
                console.log(data)
            })
        }
    }
}
